import React from 'react';
import { NavLink } from 'react-router-dom';
import './home.css';

const Comp = (props)=>{
    return(
      <>
          <section id="header" className="d-flex align-items-center">
          <div className="container-fluid">
            <div className="row">
                <div className="col-10 mx-auto">
                   <div className="row">
                   <div className="col-md-6 pt-3 pt-lg-0 order-2 0rder-lg-1 d-flex justify-content-center flex-column">
                   <h1>{props.name} <strong className="brand-name">Woodland Zoo</strong></h1>
                   <h2 className="my-3">
                     Rediscover our wildlife parks with tips and deals!
                   </h2>
                   <div className="mt-3">
                       <NavLink to={props.visit} className="btn-get-started" >{props.btnname}</NavLink>
                   </div>
                   </div>

                   <div className="col-lg-6 order-1 order-lg-2 header-img">
                   <img src={props.imgsrc} className={props.br} alt=""/>
                   </div>

                   </div>
                       
                </div>
            </div>
        </div>

          </section>
      </>
    )
}

export default Comp;